import '../stylus/app.styl'

var i_name = false;
var i_pref = false;
var i_mail = false;
var valFlg = false;
var mail_vali = /^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/;

// 
const FormApp = React.createClass({
    // disable
    getInitialState() {
        return {
            disabled: 'disabled',
            errorCheck_name: '',
            errorCheck_mail: ''
        };
    },
    // 
    Validation(type, val, event){
        // 
        switch(type) {
            // namename
            case "name":
                if(val){
                    i_name = true;
                    // blur
                    if(event.type === 'blur') {
                        this.setState({
                            errorCheck_name: ''
                        });
                    }
                } else {
                    i_name = false;
                    // blur
                    if(event.type === 'blur') {
                        this.setState({
                            errorCheck_name: 'error_check'
                        });
                    }
                }
                break;
            // namepref
            case "pref":
                if(!val.match("")){
                    i_pref = true;
                    // blur
                    if(event.type === 'blur') {
                        this.setState({
                            errorCheck_pref: ''
                        });
                    }
                } else {
                    i_pref = false;
                    // blur
                    if(event.type === 'blur') {
                        this.setState({
                            errorCheck_pref: 'error_check'
                        });
                    }
                }
                break;
            // namemail
            case "mail":
                if(val.match(mail_vali)){
                    i_mail = true;
                    // blur
                    if(event.type === 'blur') {
                        this.setState({
                            errorCheck_mail: ''
                        });
                    }
                } else {
                    i_mail = false;
                    // blur
                    if(event.type === 'blur') {
                        this.setState({
                            errorCheck_mail: 'error_check'
                        });
                    }
                }
                break;
        }

        // 
        if(i_name === true && i_pref === true && i_mail === true ){
            valFlg = true;
        } else {
            valFlg = false;
        }

        // valFlgtruedisable
        // stateprops
        if(valFlg === true){
            this.setState({
                disabled: "",
                sendVal:""
            });
        } else {
            this.setState({
                disabled: "disabled",
                sendVal:""
            });
        }
    },
    render() {
        // requiredValue={this.Validation} 
        return (
            <div className="form-entry">
                <table>
                    <ImageUpload />
                    <FormInputRequired requiredValue={this.Validation} title={''} type={'text'} name={'name'} ph={''} error={''} erch={this.state.errorCheck_name} />
                    <FormPref requiredValue={this.Validation} error={''} erch={this.state.errorCheck_pref} />
                    <FormInputRequired requiredValue={this.Validation} title={''} type={'email'} name={'mail'} ph={'sample@sample.com'} error={''} erch={this.state.errorCheck_mail} />
                </table>
                <FormSend value={this.state.sendVal} disabled={this.state.disabled} />
            </div>
        );
    }
});


// input
var FormInputRequired = React.createClass({
    // 
    getDefaultProps: function() {
        return {
            type: 'text'
        };
    },
    // porps
    propTypes:{
        title: React.PropTypes.string,
        type: React.PropTypes.string.isRequired,
        name: React.PropTypes.string,
        ph: React.PropTypes.string
    },
    // 
    requiredValidat(event){
        // name
        var type = event.target.name;
        // 
        var val = event.target.value;
        // 
        this.props.requiredValue(type, val, event);
    },
    render() {
        return (
            <tr>
                <th>{this.props.title}<span className="required"></span></th><td><input type={this.props.type} name={this.props.name} ref={this.props.name} placeholder={this.props.ph}
                                                                                          value={this.props.value} onChange={this.requiredValidat} onBlur={this.requiredValidat} required
            /><span className={this.props.erch}>{this.props.error}</span></td>
            </tr>
        );
    }
});

// 
var FormPref = React.createClass({
    getDefaultProps() {
        return {
            // 
            answers: ["", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", ""]
        };
    },
    // porps
    propTypes:{
        name: React.PropTypes.string,
    },
    // 
    requiredValidat(event){
        // name
        var type = event.target.name;
        // 
        var val = event.target.value;
        // 
        this.props.requiredValue(type, val, event);
    },
    render() {
        var options = this.props.answers.map(function(answer) {
            return <option value={answer} key={answer}>{answer}</option>;
        });
        return (
            <tr>
                <th><span className="required"></span></th>
                <td>
                    <select name="pref" name="pref" ref={this.props.name} onChange={this.requiredValidat} onBlur={this.requiredValidat} required>
                        {options}
                    </select>
                    <span className={this.props.erch}>{this.props.error}</span>
                </td>
            </tr>
        );
    }
});

// 
var FormSend = React.createClass({
    propTypes:{
        disabled: React.PropTypes.string
    },
    // disable
    getDefaultProps() {
        return {
            disabled: 'disabled',
            value:""
        };
    },
    render() {
        return (
            <p className="btn"><input type="submit" value={this.props.value} disabled={this.props.disabled} /></p>
        );
    }
});



